<template>
    <v-timeline dense>
        <v-timeline-item
                v-for="(employer, i) in employers"
                :key="i"
                :color="employer.color || 'black'"
                small
        >
            <!--<template v-slot:opposite :class="`${employer.color}&#45;&#45;text`">-->

            <div class="py-3">
                <h2 :class="`headline font-weight-light mb-3 ${employer.color}--text`">

                    <v-layout row>
                        <LinkComponent v-if="employer.companyKey" v-bind:theKey="employer.companyKey"
                                       class="headline font-weight-bold"/>
                        <span :class="`duration ${employer.color}--text`">&nbsp;&nbsp;- {{employer.duration}}
                            <span v-if="i===0"> (en cours)</span>
                        </span>

                        <v-flex>
                            <div :class="{'employer-skills': true, 'first':i===0}">
                            <span v-for="(skill, ii) in getSkills(employer.missions).skills" v-bind:key="ii">
                                <LinkComponent v-bind:theKey="skill" :displayName="false"/>
                            </span>
                            </div>
                        </v-flex>

                    </v-layout>
                    <div class="capacity">{{employer.capacity}}

                    </div>
                </h2>


                <div v-for="(mission,i) in employer.missions" :key="i">
                    <b>
                        <LinkComponent v-bind:theKey="mission.companyKey"/>
                        {{mission.companyKey ? '-':''}}</b>
                    {{mission.capacity}}
                    <span class="duration2">({{mission.duration}})</span>
                    <div v-for="(practice,j) in mission.practices" :key="j" class="practice">
                        <span class="dot"></span>{{practice}}
                    </div>
                    <span class="keyword" v-if="mission.keywords.length > 0"><span>{{root.keywordsLabel}}</span> {{formatKeyWords(mission.keywords)}}</span>
                </div>

            </div>
            <div class="date" :class="`${employer.color}--text`">{{employer.startDate}}</div>
        </v-timeline-item>
    </v-timeline>
</template>

<script>
    import LinkComponent from './LinkComponent';

    const skillsLinkKeys = ['js', 'typescript', 'angular2', 'react', 'vuejs', 'nodejs', 'git', 'cs', 'windows', 'linux'];
    const sortWithOrder = (order) => {
        return function (a, b) {
            const indexA = order.indexOf(a);
            const indexB = order.indexOf(b);
            if (indexA < 0 && indexB < 0) return -1;
            if (indexA < 0) return 1;
            if (indexB < 0) return -1;
            return indexA - indexB;
        };
    };

    export default {
        name: 'linkComponent',
        props: ['data'],
        components: {LinkComponent},
        methods: {
            formatKeyWords: function (keywords) {
                const myKeywords = this.$dataService.getAll().orderedKeywords;
                return keywords
                    .filter(x => myKeywords.includes(x))
                    .sort(sortWithOrder(myKeywords))
                    .map(k => this.$dataService.getAll().keywords[k])
                    .join(', ');
            },
            // formatDate: function (str = '') {
            //     return str.split(' ').map(x => {
            //         return {txt: x, isNb: /\d/.test(x)}
            //     });
            // },
            getSkills: function (missions) {
                let skills = [];
                missions.forEach(mission => {
                    skills = mission.skillsKeys.concat(skills);
                });
                skills = [...new Set(skills)];
                skills = skills
                    .filter(x => skillsLinkKeys.includes(x))
                    .sort(sortWithOrder(skillsLinkKeys));
                return {
                    skills: [...new Set(skills)]
                }
            }
        },
        data: function () {
            return {
                employers: this.$dataService.getEmployers(),
                root: this.$dataService.getAll(),
            }
        }
    }
</script>

