<script>
    let isPdf = false;
    import TimelineComponent from './components/TimelineComponent';
    import LinkComponent from './components/LinkComponent';
    import DataService from './services/dataService';
    import Vue from 'vue';

    Vue.prototype.$dataService = new DataService();
    if (window.location.href.includes('en')) {
        Vue.prototype.$dataService.changeLang('en');
    }
    export default {
        name: 'App',
        components: {
            // MissionComponent,
            LinkComponent,
            TimelineComponent
        },
        created: function () {
            if (window.location.href.includes('pdf')) {
                isPdf = true;
            }
        },
        data: function () {
            const self = this;
            const skills = ['js', 'typescript', 'angular2', 'react', 'vuejs', 'nodejs', 'git', 'css3', 'html5', 'windows', 'linux']
                .map(key => {
                    return {
                        key,
                        note: self.$dataService.getSkill(key),
                    };
                });

            let exportUrl = 'https://us-central1-shareyourbooks-254022.cloudfunctions.net/urltopdf/urltopdf/https:@s@@s@alban44.github.io@q@pdf/CV_Alban_Alnot';
            if(window.location.href.includes('localhost')){
                exportUrl = 'http://localhost:4000/urlToPdf/http:@s@@s@localhost:8080@q@pdf/cv-local';
            }

            return {
                skills,
                miscs: this.$dataService.getMiscs(),
                trainings: this.$dataService.getTrainings(),
                personnalStuff: this.$dataService.getPersonalStuffs(),
                root: this.$dataService.getAll(),
                componentKey: 0,
                exportUrl
            }
        },
        methods: {
            switchLang: function () {
                this.$dataService.changeLang();
            },
            isPdf: () => {
                return isPdf;
            },
            formatKeySkills: (skills) => {
                return skills
                    .filter(x => ['js', 'nodejs', 'react', 'vuejs', 'git'/*, 'html5', 'css3'*/].includes(x));
            }
        }
    }
</script>
